<script>
import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

var max = (a, b) => (a > b ? a : b);
var min = (a, b) => (a < b ? a : b);

function loadRecords() {
  var recordsString = window.localStorage.getItem("records");
  var records = null;
  if (recordsString == null) {
    records = [
      {
        id: "rec#1",
        label: "the very-very first record."
      },
      {
        id: "rec_2",
        label: "the most second record!"
      },
      {
        id: "rec!3",
        label: "it looks like a pretty good place to leave short notes"
      }
    ];
    saveRecords(records);
  } else {
    records = JSON.parse(recordsString);
  }
  return records;
}

function saveRecords(records) {
  window.localStorage.setItem("records", JSON.stringify(records));
}

var records = loadRecords()
var pageSize = 10

export default new Vuex.Store({
  state: {
    records,
    pageSize,
    offset: records.length - pageSize
  },
  getters: {
    records: state =>
      state.records.slice(state.offset, state.offset + state.pageSize),
    page: state => state.page,
    hasLess: state => state.offset > 0,
    hasMore: state => state.records.length > state.pageSize + state.offset
  },
  mutations: {
    newRecord: (state, label) => {
      state.records.push({
        id: "rec$" + state.records.length,
        label
      });
      state.offset = max(state.records.length - state.pageSize, 0);
      saveRecords(state.records);
    },
    deleteRecord: (state, id) => {
      state.records = state.records.filter(record => record.id != id);
      state.offset = max(state.records.length - state.pageSize, 0);
      saveRecords(state.records);
    },
    prevPage: state => (state.offset = max(state.offset - state.pageSize, 0)),
    nextPage: state =>
      (state.offset = min(
        state.offset + state.pageSize,
        state.records.length - state.pageSize
      ))
  }
});
</script>


