<template>
  <table class="pure-table-none wide">
    <tr>
      <td>
        <button class="pure-button" style="color: #FFF; background-color: #FFF" disabled>ðŸ—‘</button>
      </td>
      <td class="wide">
        <button class="pure-button wide"
                @click="prevPage(store)"
                :disabled="!store.getters.hasLess">â€¢â€¢â€¢</button>
      <td>
        <button class="pure-button" style="color: #FFF; background-color: #FFF" disabled>ðŸ—’</button>
      </td>
    </tr>

    <tr v-for="record in store.getters.records" :key="record.id">
      <td>
        <button class="pure-button warning"
                @click="deleteRecord(store, record.id)">ðŸ—‘</button>
      </td>
      <td>
        {{ record.label }}
      </td>
      <td>
        <!-- reserve a special place here -->
      </td>
    </tr>

    <tr v-if="store.getters.hasMore">
      <td>
        <button class="pure-button" style="display: none" disabled/>
      </td>
      <td>
        <button class="pure-button wide"
                @click="nextPage(store)">â€¢â€¢â€¢</button>
      </td>
      <td>
        <button class="pure-button" style="display: none" disabled/>
      </td>
    </tr>
    <tr v-else>
      <td></td>
      <td>
        <input type="text" placeholder="..."
               v-model="label"
               @keypress.enter="newRecord(store)"/>
      </td>
      <td>
        <button class="pure-button pure-button-primary"
                :disabled="label.length == 0"
                @click="newRecord(store)">ðŸ—’</button>
      </td>
    </tr>

  </table>
</template>

<script>
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default {
  props: {
    store: {
      type: Vuex.Store,
      required: true
    },
    addEnabled: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    nextPage: store => store.commit("nextPage"),
    prevPage: store => store.commit("prevPage"),
    newRecord: function(store) {
      if (this.label.length > 0) {
        store.commit("newRecord", this.label);
        this.label = "";
      }
    },
    deleteRecord: (store, id) => store.commit("deleteRecord", id)
  },
  data: function() {
    return {
      label: ""
    };
  }
};
</script>
<style>
.wide {
  width: 100%;
}

input[type=text] {
  width: 97%;
}
</style>



