<template>
  <div id="app" class="pure-g">
    <div class="pure-u-1-3"></div>
    <div class="pure-u-1-3">
      <records :store="store"/>
    </div>
    <div class="pure-u-1-3"></div>
  </div>
</template>

<script>
import records from './components/Records.vue'
import store from './Store.vue'

export default {
  name: 'app',
  data: function() {return{
    store: store
  }},
  components: {
    records
  }
}
</script>



